import React, { Component } from "react";
import junta from "../data/junta-directiva.json";

class JuntaDirectiva extends Component {
	renderTeam(team, key) {
		return (
			<div className="junta-team" key={key}>
				<h4>{team.titol}</h4>
				{
					team.components.map((p, i) => {
						return <p key={i}>{p.carrec ? <><span>{p.carrec}</span>: </> : <></>}{p.nom}</p>;
					})
				}
			</div>
		);
	}
	render() {
		const dirreccio = junta.actual.direccio;
		const equips = junta.actual.junta;
		const col_1 = equips.slice(0, Math.ceil(equips.length / 2));
		const col_2 = equips.slice(Math.ceil(equips.length / 2), equips.length);

		return (<>
			<section>
                <h2>Junta directiva</h2>

				<p>
					La Junta Directiva est dirigida per l'equip de presidncia. Ha de constar tamb de secretaria, tresoreria, comissi tcnica i comissi feminista. Actualment, tamb existeixen les comissions: material, acolliment social, msics, logstica i contractaci, promoci i difusi, equip sanitari i activitats.
				</p>

				<p>
					La funci de la Junta Directiva s representar, dirigir i administrar l'associaci. Es controla el funcionament de la colla, el balan i l'estat de comptes, s'organitzen les activitats i espais que es puguin necessitar. Tamb es mant el control de les altes i baixes dins la colla i es convoquen assemblees.
				</p>

				<div className="juntes">
					<div className="junta-column">
						{
							this.renderTeam(dirreccio, 0)
						}
					</div>
					<div className="junta-column">
						{
							col_1.map((t, i) => {
								return this.renderTeam(t, i);
							})
						}
					</div>
					<div className="junta-column">
						{
							col_2.map((t, i) => {
								return this.renderTeam(t, i);
							})
						}
					</div>
				</div>
			</section>
		</>);
	}
}

export default JuntaDirectiva;
